import React, { Component } from 'react';
import CryptoJS from 'crypto-js';
import '../styles/result.css';
import Question from '../components/Question';

class Result extends Component {
	constructor() {
		super();
		this.state = {
			result: null
		}
	}

	componentDidMount(){
		try {
			const urlParams = new URLSearchParams(window.location.search);
			let result = JSON.parse(CryptoJS.AES.decrypt(decodeURIComponent(urlParams.get('result')), 'result').toString(CryptoJS.enc.Utf8));
			console.log(result);
			this.setState({result});
	    } catch (e) {}
	}

	render() {
		let result = this.state.result;
		let content = "please wait";
		
		if(result) {
			let questions = result.questions.map((question, i) => {
				return (
					<Question
						key={i}
						id={i}
						i={i}
						content={question.content}
						options={question.options}
						feedback={question.feedback}
						categoryId={2}
						onChooseOption={() => {}}/>
				)                 
			});

			content =
			<div>
				<div>
					<div>
						<span>Title :</span>
						<span>{result.section}</span>
					</div>
					<div>
						<span>Points :</span>
						<span>{result.correct} of {result.correct + result.incorrect}</span>
					</div>
					<div>
						<span>Percentage :</span>
						<span>{result.score}</span>
					</div>
				</div>
				<div>{questions}</div>
			</div>
		}

		return (
			<div className="result page">
				{content}
			</div>
		);
	}
}

export default Result;
